// top
.top
	padding-bottom: 56px
	background: url(../img/svg/top-bg-right.svg) no-repeat 96% 73%, url(../img/svg/top-bg-left.svg) no-repeat 10% 77%, $yellow
	&__in
		position: relative
		margin: 4.2% auto 0
		max-width: 790px
		text-align: center
		.video
			margin-top: 60px

// price
.price
	position: relative
	text-align: center
	background: url(../img/svg/price-bg-right.svg) no-repeat 95% 35%, url(../img/svg/price-bg-left.svg) no-repeat 9% 58%, $yellow
	&:before
		position: absolute
		content: ''
		left: 50%
		bottom: -103px
		margin-left: -103px
		+arr(206,103,$yellow,b)
	&:after
		position: absolute
		content: ''
		left: 50%
		bottom: -90px
		margin-left: -36px
		+svg(price-arr,72,55)
	.circle
		position: absolute
		top: -76px
		left: 50%
		margin-left: -76px
	.title
		margin-bottom: 76px
	.time-day,
	.time,
	.cost
		min-width: 83px
		text-align: left
		font-size: 19px
	.time
		width: 220px
		text-align: center
	.cost
		padding-left: 22px
		width: 105px
	&__in
		position: relative
		padding: 160px 15px
		@extend %cent
		overflow: hidden
	&__text
		margin-bottom: 20px
		letter-spacing: .05em
	&__row
		margin-top: 10px
		padding-left: 30px
		font-size: 0
	&__btn
		margin-top: 44px
	&__foot
		margin-top: 60px
		+lb
		font-size: 24px
		line-height: 1.2

// course
.course
	padding: 188px 0 105px
	color: $white
	text-align: center
	background: url(../img/svg/course-bg-left.svg) no-repeat 9% 61%, url(../img/svg/course-bg-right.svg) no-repeat 92% 53%
	.title
		margin-bottom: 80px
	.wrap
		text-align: left
	.title-small
		margin: 86px 0 55px
	&__in
		@extend %cent

// video
.video
	&__btn
		font-size: 0
		transition: transform .2s, color .2s
		background: transparent
		&:hover
			transform: scale(1.1)
			color: $white
		.icon
			transform: translate3d(0,0,0)
			font-size: 61px

// steps
.steps
	position: relative
	width: 353px
	height: 353px
	border: 10px solid rgba($white, .1)
	border-radius: 50%
	vertical-align: top
	display: inline-block
	&:before,
	&:after
		position: absolute
		content: ''
		top: 50%
		left: 50%
		transform: translate(-50%,-50%)
		border-radius: 50%
	&:before
		height: 112px
		width: 112px
		background: $yellow
	&:after
		height: 157px
		width: 157px
		border: 10px solid rgba($yellow, .3)
	&__item
		position: absolute
		left: -10px
		font-size: 22px
		+lb
		opacity: .5
		&:before
			position: absolute
			content: ''
			height: 48px
			width: 48px
			border: 10px solid rgba($white, .1)
			border-radius: 50%
		&:first-child
			top: -17px
			&:before
				bottom: -54px
				right: -28px
		&:nth-child(2)
			top: -17px
			&:before
				bottom: -54px
				left: -28px
		&:nth-child(3)
			bottom: -17px
			&:before
				top: -56px
				right: -28px
		&:nth-child(4)
			bottom: -17px
			&:before
				top: -54px
				left: -28px
		&:nth-child(even)
			left: auto
			right: -10px
	&__text
		letter-spacing: .05em

// graph
.graph
	position: relative
	padding: 178px 15px 120px
	text-align: center
	overflow: hidden
	background: url(../img/svg/graph-bg-left.svg) no-repeat 7% 41%, url(../img/svg/graph-bg-right.svg) no-repeat 92% 57%, $yellow
	&:before,
	&:after
		position: absolute
		content: ''
		left: 50%
		transform: translateX(-50%)
	&:before
		top: -102px
		width: 204px
		height: 204px
		border-radius: 50%
		background: $dark-gray
	&:after
		top: 26px
		+svg(graph-arr,484,44)
	.wrap
		margin: 78px 0 75px
	&__in
		@extend %cent

// textbook
.textbook
	position: relative
	padding: 197px 0 100px
	color: $white
	text-align: center
	overflow: hidden
	background: url(../img/svg/textbook-bg-left.svg) no-repeat 7% 10%, url(../img/svg/textbook-bg-right.svg) no-repeat 93% 11%
	&:before
		position: absolute
		content: ''
		top: -45px
		left: 50%
		margin-left: -101px
		z-index: 2
		+svg(graph-bot-arr,202,159)
	.title
		margin-bottom: 75px
	.title-small
		margin-top: 75px
	&__in
		@extend %in
	&__wrap
		@extend %cent
	&__bot
		margin-top: 80px
		+clr
	&__left
		float: left
		padding-right: 121px
		width: 42%
		+lb
		font-size: 24px
		text-align: right
		letter-spacing: .05em
	&__right
		float: right
		padding-top: 20px
		width: 58%
		text-align: left

// slider
.slider
	position: relative
	margin: 93px auto 0
	padding: 0 88px
	max-width: 1192px
	&__arr
		position: absolute
		top: 0
		left: 15px
		right: 15px
		bottom: 0
	&__btn
		position: absolute
		top: 50%
		margin-top: -30px
		font-size: 0
		opacity: .5
		will-chenge: opacity
		transition: opacity .2s
		color: $white
		background: transparent
		&:hover
			opacity: 1
		&:active
			opacity: .5
		&.is-prev
			left: 0
		&.is-next
			right: 0
		.icon
			font-size: 60px

// contacts
.contacts
	background: $yellow
	+clr
	.title-middle
		margin-bottom: 42px
	.label
		margin-top: 22px
	.title-small
		margin-top: 60px
	.soc
		margin-top: 48px
	&__col
		float: left
		width: 50%
	&__in
		padding: 53px 20px 68px 136px
		max-width: 510px
	&__form
		margin: 15px 0 28px
		font-size: 0
		.btn
			margin-left: 35px
			vertical-align: top
	&__input
		width: 203px
		vertical-align: top
		display: inline-block

// map
.map
	width: 100%
	height: 796px
	.gm-style-cc
		display: none
	.gm-style
		a
			display: none !important
		div
			background-color: transparent !important
			box-shadow: none !important
			border: none !important
	.gm-style-iw
		top: -73px !important
		left: 37px !important
		color: $white
		letter-spacing: 0.1em
		font-size: 24px
		+lb
		& + div
			display: none